/* eslint-disable no-unused-vars */
import React, { Component,useState, createRef} from 'react';
import "./CertificationCard.scss";
import {Fade} from 'react-reveal';
import ColorThief from "colorthief";



export default function CertificationCard ({certificate, isDark}) {
    
        // eslint-disable-next-line react-hooks/rules-of-hooks
        const [colorArrays, setColorArrays] = useState([]);
  const imgRef = createRef();

  function getColorArrays() {
    const colorThief = new ColorThief();
	var colors = colorThief.getColor(imgRef.current);
	colors[0] = colors[0] + 150;
	colors[1] = colors[1] + 150;
	colors[2] = colors[2] + 150;
    setColorArrays(colors);
	console.log(colorThief.getColor(imgRef.current));
  }

  function rgb(values) {
    return typeof values === "undefined"
      ? null
      : "rgb(" + values.join(", ") + ")";
  }
        // const certificate = this.props.certificate;
        return (
			<Fade bottom duration={1000} distance="20px">
					<div className="cert-card">
						<div className="content">
							<a href={certificate.certificate_link} target="_blank" rel="noopener noreferrer">
								<div className="content-overlay"></div>
									<div className="cert-header" style={{ backgroundColor: rgb(colorArrays)}}>
										<img
											ref={imgRef}
											className="logo_img"
											src={require(`../../assets/images/${certificate.logo_path}`)}
											alt={certificate.alt_name}
											onLoad={() => getColorArrays()}
										/>
									</div>
								<div className="content-details fadeIn-top">
									<h3 className="content-title">Certificate</h3>
								</div>
							</a>
						</div>
						<div className="cert-body">
							<h2 className="cert-body-title">{certificate.title}</h2>
							<h3 className="cert-body-subtitle">{certificate.subtitle}</h3>
						</div>
					</div>
			</Fade>
				);
    }
