import React, {useContext} from "react";
import "./ValueProposition.scss";
import {valueProposition} from "../../portfolio";
import {Fade} from "react-reveal";
import StyleContext from "../../contexts/StyleContext";

export default function ValueProposition() {
  const {isDark} = useContext(StyleContext);
  
  if (!valueProposition.display) {
    return null;
  }

  return (
    <div className={isDark ? "dark-mode main" : "main"} id="valueproposition">
      <div className="value-prop-main-div">
        <Fade bottom duration={1000} distance="20px">
          <h1 className={isDark ? "dark-mode value-prop-heading" : "value-prop-heading"}>
            {valueProposition.title}
          </h1>
          <p className={isDark ? "dark-mode value-prop-subtitle" : "value-prop-subtitle"}>
            {valueProposition.subtitle}
          </p>
          <div className="value-prop-grid">
            {valueProposition.propositions.map((prop, i) => (
              <Fade bottom duration={1000} distance="20px" delay={i * 100} key={i}>
                <div className={isDark ? "value-prop-card dark-mode" : "value-prop-card"}>
                  <div className="value-prop-icon">{prop.icon}</div>
                  <h3 className="value-prop-card-title">{prop.title}</h3>
                  <p className="value-prop-card-description">{prop.description}</p>
                </div>
              </Fade>
            ))}
          </div>
        </Fade>
      </div>
    </div>
  );
}
