import React, {useContext} from "react";
import "./MetricsCard.scss";
import StyleContext from "../../contexts/StyleContext";

export default function MetricsCard({metric}) {
  const {isDark} = useContext(StyleContext);
  
  return (
    <div className={isDark ? "metrics-card dark-mode" : "metrics-card"}>
      <div className="metrics-number">{metric.number}</div>
      <div className="metrics-label">{metric.label}</div>
      <div className="metrics-description">{metric.description}</div>
    </div>
  );
}
