import React, {useContext} from "react";
import "./KeyMetrics.scss";
import MetricsCard from "../../components/metricsCard/MetricsCard";
import {keyMetrics} from "../../portfolio";
import {Fade} from "react-reveal";
import StyleContext from "../../contexts/StyleContext";

export default function KeyMetrics() {
  const {isDark} = useContext(StyleContext);
  
  if (!keyMetrics.display) {
    return null;
  }
  
  return (
    <div className={isDark ? "dark-mode main" : "main"} id="metrics">
      <div className="metrics-main-div">
        <Fade bottom duration={1000} distance="20px">
          <h1 className={isDark ? "dark-mode metrics-heading" : "metrics-heading"}>
            {keyMetrics.title}
          </h1>
          {keyMetrics.subtitle && (
            <p className={isDark ? "dark-mode metrics-subtitle" : "metrics-subtitle"}>
              {keyMetrics.subtitle}
            </p>
          )}
          <div className="metrics-grid">
            {keyMetrics.metrics.map((metric, i) => {
              return (
                <Fade bottom duration={1000} distance="20px" delay={i * 100} key={i}>
                  <MetricsCard metric={metric} />
                </Fade>
              );
            })}
          </div>
        </Fade>
      </div>
    </div>
  );
}
